import React from "react";
import styles from "./registration.module.css";
import CustomInput from "../customInput/CustomInput";

interface PropTypes {}

const Registration: React.FC<PropTypes> = () => {
  return (
    <div className={styles.registration}>
      <div className={styles.inputGroup}>
        <CustomInput
          theme="dark"
          label=":"
          name="lastName"
          id="lastName"
        />
      </div>
      <div className={styles.inputError}><p>  </p></div>
      <div className={styles.inputGroup}>
        <CustomInput
          theme="dark"
          label="':"
          name="firstName"
          id="firstName"
        />
      </div>
      <div className={styles.inputGroup}>
        <CustomInput
          theme="dark"
          label="-:"
          name="patronymic"
          id="patronymic"
        />
      </div>
      <div className={styles.inputGroup}>
        <CustomInput
          theme="dark"
          label=":"
          name="password"
          id="password"
          type="password"
        />
      </div>
      <div className={styles.inputGroup}>
        <CustomInput
          theme="dark"
          label=" :"
          name="passwordRepeat"
          id="passwordRepeat"
          type="password"
        />
      </div>
      <div className={styles.inputGroup}>
        <CustomInput
          theme="dark"
          label="E-mail:"
          name="email "
          id="email"
        />
      </div>
      <div className={styles.inputGroup}>
        <CustomInput
          theme="dark"
          label=":"
          name="phoneNumber"
          id="phoneNumber"
        />
      </div>
    </div>

  );
};

export default Registration;
